import * as actionType from './actionType'
import {getData} from 'util'
import {API_CHANGE_ORDER_PAGE, API_CHANGE_ORDER_EXECUTE,
  API_NEWPRODUCT_GETPRODUCTKEYVALUES_MORE,
  API_CHANGE_ORDER_CANCEL, API_CHANGE_ORDER_DET, API_CHANGE_ORDER_CREATE, API_FRMLOSSDTLORDERS_GETSTORAGELOCATIONFUZZYKEYVALUES, API_GET_MENUS_PERMISSIONS_BANS, API_CHANGE_ORDER_EXPORT, API_CHANGE_ORDER_GETPROPORTION, API_CHANGE_GETPRODUCTLIST} from 'api'
//同步
export const update = (object) => ({
  type: actionType.GOODSCHANGE_UPDATE,
  data: object
})
import moment from 'moment'

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  dispatch({type: actionType.GOODSCHANGE_UPDATE, data: {tableLoading: true}})
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginTime = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endTime = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_CHANGE_ORDER_PAGE, ...reqData}).then(data => {
    // 相应的逻辑判断
    let {page, size} = reqData.searchParams
    dispatch({
      type: actionType.GOODSCHANGE_UPDATE,
      data: {
        page,
        size,
        tableLoading: false,
        total: data.result.total,
        selectedRowKeys: [], // 选中的队列key
        selectedRows: [], // 选中的队列数据
        searchResult: data.result.list || []
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.GOODSCHANGE_FILE
    })
  })
}

// 获取储位精准信息
export const handleChuweiInfo = (reqData, callBack) => (dispatch) => {
  dispatch({type: actionType.GOODSCHANGE_UPDATE, data: {createTableLoading: true}})
  let api = API_FRMLOSSDTLORDERS_GETSTORAGELOCATIONFUZZYKEYVALUES
  getData({api, ...reqData}).then(data => {
    // 相应的逻辑判断
    dispatch({
      type: actionType.GOODSCHANGE_UPDATE,
      data: {
        createTableLoading: false
      }
    })
    callBack && callBack(data.result)
  }).catch(err => {
    dispatch({
      type: actionType.GOODSCHANGE_FILE
    })
  })
}

