// 获取页面按钮权限
export const GetButtonPower = (reqData) => (dispatch) => {
  getData({api: API_GET_MENUS_PERMISSIONS_BANS, ...reqData}).then(({data}) => {
    // 相应的逻辑判断
    let Power = {}
    data.map((o) => {
      Power[o.menuCode] = true
    })
    dispatch({
      type: actionType.GOODSCHANGE_UPDATE,
      data: {
        ...Power
      }
    })
  }).catch(err => {
    dispatch({
      type: actionType.GOODSCHANGE_FILE
    })
  })
}

//报损导出列表
export const handleExport = (reqData, callBack) => (dispatch) => {
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) === '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  if (reqObj.initDate && reqObj.initDate.length) {
    reqObj.beginTime = moment(reqObj.initDate[0]).format('YYYY-MM-DD')
    reqObj.endTime = moment(reqObj.initDate[1]).format('YYYY-MM-DD')
  }
  delete reqObj.initDate
  getData({api: API_CHANGE_ORDER_EXPORT, ...reqData}).then(data => {
    callBack && callBack()
  }).catch(err => {
    dispatch({
      type: actionType.REQUISITIONSORDERS_FILE
    })
  })
}

