import React, {PureComponent} from 'react'
import {
  Modal,
  Table,
  Row,
  Col
} from 'antd'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  state => ({...state.goodsChange, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {

    }
    this.columns = [{
      title: '原商品编码',
      width: 100,
      dataIndex: 'sourceProductCode',
      key: 'sourceProductCode'
    }, {
      title: '原商品名称',
      width: 100,
      dataIndex: 'sourceProductName',
      key: 'sourceProductName'
    }, {
      title: '转换编码',
      width: 100,
      dataIndex: 'targetProductCode',
      key: 'targetProductCode'
    }, {
      title: '转换商品',
      width: 100,
      dataIndex: 'targetProductName',
      key: 'targetProductName'
    }, {
      title: '储位',
      width: 100,
      dataIndex: 'storeLocationCode',
      key: 'storeLocationCode'
    }, {
      title: '消耗数量',
      width: 100,
      dataIndex: 'sourceQty',
      key: 'sourceQty'
    }, {
      title: '转换比例',
      width: 100,
      dataIndex: 'proportion',
      key: 'proportion'
    }, {
      title: '转换数量',
      width: 100,
      dataIndex: 'targetQty',
      key: 'targetQty'
    }, {
      title: '单价（不含税）',
      width: 100,
      dataIndex: 'price',
      key: 'price'
    }, {
      title: '总价（不含税）',
      width: 100,
      dataIndex: 'amount',
      key: 'amount'
    }]
  }
  componentDidMount() {
    this._getAsyncData()
  }
  // 根据tabs的key来判断请求哪个接口
  _getAsyncData() {
    const {handleGetDetailRecord, match: {params}, history} = this.props
    handleGetDetailRecord({urlParams: [params.id]})
  }
  // 关闭弹窗
  handleCloseModal = () => {
    const {history} = this.props
    history.length > 1 ? history.goBack() : window.close()
  }

  render() {
    const {
      tableRecordLoading,
      searchResultRecord
    } = this.props
    let recordData = searchResultRecord.header ? searchResultRecord.header : ''
    return (
      <div className='fullOrderDel'>
        <span className='ant-modal-close-x close' onClick={this.handleCloseModal}></span>
        <div style={{paddingLeft: 20}}>
          <Row>
            <Col span={8}><h4>货主：{recordData.shipper}</h4></Col>
            <Col span={8}><h4>转换类型：{recordData.changeType}</h4></Col>
            <Col span={8}><h4>转换总额：{recordData.amount}</h4></Col>
            <Col span={24}><h4>备注：{recordData.remark}</h4></Col>
          </Row>
        </div>
        <div className='table-group' style={{margin: 0}}>
          <Table
            rowKey={record => record.id}
            loading={tableRecordLoading}
            columns={this.columns}
            scroll={{x: 'max-content', y: 400}}
            dataSource={searchResultRecord.items}
            pagination={false}
          />
        </div>
      </div>
    )
  }
}



